/**
 * @module ext-bb-user-context-menu-ng
 *
 * @description
 * Default extension for User Context Menu widget.
 *
 * @requires ui-bb-avatar-ng
 * @requires ui-bb-i18n-ng
 * @requires vendor-bb-angular-ng-aria
 *
 * @example
 * <!-- User Context Menu widget model.xml -->
 * <property name="extension" viewHint="text-input,admin">
 *  <value type="string">ext-bb-user-context-menu-ng</value>
 * </property>
 */
import uiBbAvatarKey from 'ui-bb-avatar-ng';
import i18nKey from 'ui-bb-i18n-ng';
import ngAriaModuleKey from 'vendor-bb-angular-ng-aria';
import uibPopoverKey from 'vendor-bb-uib-popover';
import uiBbModalKey from 'ui-bb-modal-ng';
import uiBbAutocompleteSearchKey from 'ui-bb-autocomplete-search-ng';
import uiBbEllipsisTooltipKey from 'ui-bb-ellipsis-tooltip-ng';

import extHooks from './hooks';
import extHelpers from './helpers';

export const dependencyKeys = [
  uiBbAvatarKey,
  i18nKey,
  ngAriaModuleKey,
  uibPopoverKey,
  uiBbModalKey,
  uiBbAutocompleteSearchKey,
  uiBbEllipsisTooltipKey,
];

export const hooks = extHooks;
export const helpers = extHelpers;
export default dependencyKeys;

